@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  text-align: center;
  font-family: "Roboto Condensed", sans-serif;
  min-height: 100vh;
  color: #ffffff;
  background-color: #1f1f24;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.app-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 80vw;
  margin-top: 10vh;
  margin-bottom: 5vh;
}

.app h1 {
  font-weight: 700;
}

.movies-row {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  grid-gap: 15px 20px;
  gap: 15px 20px;
  flex-wrap: wrap;
  width: 100%;
}

/* SREEN SIZES ADJUSTEMENTS */
@media screen and (max-width: 1080px) {
  .movies-row {
    justify-content: center;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.submit-button {
    text-align: center;
    padding: 10px 40px 10px 40px;
    background-color: #ffa726;
    color: #FFFFFF;
    font-size: 0.6em;
    font-weight: bold;
    margin-top: 4.5vh;
    margin-bottom: 5vh;
    transition: 0.2s;
    cursor: pointer;
    border: none;
}

.submit-button:hover {
    background-color: #ffcc80;
    transition: 0.2s ease;
}

/* SREEN SIZES ADJUSTEMENTS */
@media screen and (max-width: 1020px) {
    .submit-button {
        font-size: 0.6em;
        margin-top: 3vh;
    }   
}

@media screen and (max-width: 500px) {
    .submit-button {
        font-size: 0.9em;
        margin-top: 3vh;
    }   
}
.banner {
    width: 100%;
    margin-top: 5vh;
    margin-bottom: 1vh;
    padding-top: 1vh;
    padding-bottom: 1vh;
    font-size: 1em;
    font-weight: bold;
    text-align: left;
}

/* SREEN SIZES ADJUSTEMENTS */
@media screen and (max-width: 1080px) {
    .banner {
        font-size: 1.2em;
        text-align: center;
        margin-top: 5vh;
    }
}

@media screen and (max-width: 500px) {
    .banner {
        font-size: 1.5em;
        text-align: center;
        margin-top: 3vh;
    }
}
/* Card styles depending on status */
.card-unselected {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 360px;
  width: 220px;
  margin-bottom: 2vh;
  transition: 0.2s;
}

.card-selected {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 360px;
  width: 220px;
  margin-bottom: 2vh;
  transition: 0.2s;
}

.card-excluded {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 360px;
  width: 220px;
  opacity: 0.5;
  margin-bottom: 2vh;
  transition: 0.2s;
}

/* HOVER */
.card-image-container:hover {
  background-color: #ffa726;
  transition: 0.1s;
  cursor: pointer;
}

.card-image-container:hover .card-image {
  opacity: 0.8;
}

/* Cards elements */
.card-image-container {
  background-color: #fff;
  height: 300px;
  width: 220px;
  padding: 6px;
}

.card-image-container-selected {
  background-color: #ffa726;
  height: 300px;
  width: 220px;
  padding: 6px;
}

.card-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.cart-text-container {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  height: 60px;
  width: 85%;
}

.card-title {
  font-size: 0.6em;
  font-weight: 400;
  width: 100%;
  text-align: left;
  margin-top: 0.7vh;
  line-height: 1.2;
}

/* SREEN SIZES ADJUSTEMENTS */
@media screen and (max-width: 1020px) {
  .card-title {
    font-size: 0.7em;
  }
}

@media screen and (max-width: 500px) {
  .card-title {
    font-size: 0.9em;
  }

  .card-unselected {
    margin-bottom: 1vh;
  }
  
  .card-selected {
    margin-bottom: 1vh;
  }
  
  .card-excluded {
    margin-bottom: 1vh;
  }
}

.modal-background-box {
    position: fixed;
    background: #00000050;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    margin: 0 auto;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    background: #FFFFFF;
    /* border-radius: 15px; */
    padding: 3vh;
    overflow: auto;
    box-shadow: 1px 1px 8px 1px black;
}

.modal-title {
    color: black;
    margin-bottom: 3vh;
}

.modal-text {
    color: black;
    font-size: 0.5em;
}

.modal-image {
    width: 50%;
    margin-top: 15px;
    margin-bottom: 15px;
}

.modal-close-button {
    color: black;
    background-color: #FFFFFF;
    font-size: 0.475em;
    font-weight: 500;
    border: 1px solid black;
    margin-top: 40px;
    padding: 8px 30px 8px 30px;
    transition: 0.2s ease;
    cursor: pointer;
}

.modal-close-button:hover {
    color: #CCCCCC;
    border: 1px solid #CCCCCC;
    transition: 0.2s ease;
}

/* TAILLE D'ECRAN : ajustements */
@media screen and (max-width: 1020px) {
    .modal-box {
        width:60%;
    }
    .modal-title {
        font-size: 0.8em;
    }
    .modal-text {
        font-size: 0.6em;
    }
    .modal-close-button {
        font-size: 0.6em;
    }   
}

@media screen and (max-width: 500px) {
    .modal-box {
        width:70%;
    }
    .modal-title {
        font-size: 1em;
    }
    .modal-text {
        font-size: 0.8em;
    }
    .modal-close-button {
        font-size: 0.7em;
    }   
}
